import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { Catagory } from 'src/Tabels/tabels-list';
@Injectable({
  providedIn: 'root'
})
export class CatagoryService {
  private CategoryCollection: AngularFirestoreCollection<any>;
  private Categorys: Observable<any[]>;
  constructor(db: AngularFirestore) { 
   this.CategoryCollection = db.collection<Catagory>('Category');
 
  }

 create(Category) { 
  return this.CategoryCollection.add(Category);
  //return this.db.list('/Category').push(Category);
}
getallCategorys() {
   
  this.Categorys = this.CategoryCollection.snapshotChanges().pipe(
    map(actions => {
      return actions.map(a => {
        const data = a.payload.doc.data();
        const id = a.payload.doc.id;
        return { id, ...data };
      });
    })
  );
  return this.Categorys;
}
getCategory(id) {
  return this.CategoryCollection.doc<any>(id).valueChanges();
}

updateCategory(Category: Catagory, id: string) {
  return this.CategoryCollection.doc(id).update(Category);
}

addCategory(Category: any) {
  return this.CategoryCollection.add(Category);
}

removeCategory(id) {
  return this.CategoryCollection.doc(id).delete();
}
}

